/************************************
    Ridematch - Dashboard
/***********************************/
.dashboard {
    position: relative;
    top: 4.8rem;

    h2 {
        @include font-size(16, 10);
        display: inline-block;

        .material-icons {
            @include font-size(16, 10);
            width: 1.6rem;
            height: 1.6rem;
        }
    }

    hr {
        color: $light-grey;
        background-color: $light-grey;
    }

    .card {
        border: none;

        .card {
            border: 1px solid $medium-grey;
            margin-bottom: 2rem;
        }
    }

    .well {
        background-color: $light-grey;
        border: 0;
        padding: 1rem;

        p {
            @include font-size(12, 10);
            font-weight: 700;
            margin-top: 0;
        }
    }
    @include screen ('desktop') {
        top: 0;

        .card .card {
            border: none;
            padding-top: 0;
        }

        .border-left {
            border-left: 1px solid $light-grey;
        }

        .border-right {
            border-right: 1px solid $light-grey;
        }

        .toos {
            margin-top: 1rem;

            .material-icons {
                @include font-size(15, 10);
            }

            .link a {
                @include font-size(12, 10);
            }
        }
    }
}
